// ^   localStorage:
import { configureStore } from '@reduxjs/toolkit';
import sliceFilterReducer from './filterSlice';
import { tweetsAPI } from './tweetsApi';

import sliceUserKeyReducer from './sliceUserKey';

//        localStorage:
import storage from 'redux-persist/lib/storage';

import {
  // ~  ,      -    Redux-Persist
  FLUSH,
  REHYDRATE,
  PAUSE,
  PERSIST,
  PURGE,
  REGISTER,
} from 'redux-persist';
import persistStore from 'redux-persist/lib/persistStore';
import persistReducer from 'redux-persist/es/persistReducer';

const userKeyPersistConfig = {
  key: 'userKey',
  storage,
  // whitelist: ['stateUserKey'], //           ' sliceAuth.initialState
};

// ~  RTKQuery  Store    middleware,     -
const store = configureStore({
  reducer: {
    //  - ',   export default:
    storeFilter: sliceFilterReducer,
    storeUserKey: persistReducer(userKeyPersistConfig, sliceUserKeyReducer),
    //    RTK Query  -:
    [tweetsAPI.reducerPath]: tweetsAPI.reducer,
  },

  //  RTK Query   middleware:
  // middleware   .    concat  ,    middleware  RTKQueryApi    
  // getDefaultMiddleware -  
  // ~  middleware,      -    Redux-Persist
  middleware: getDefaultMiddleware =>
    getDefaultMiddleware({
      //    :
      serializableCheck: {
        //  ,   :
        ignoredActions: [FLUSH, REHYDRATE, PAUSE, PERSIST, PURGE, REGISTER],
      },
    }).concat(tweetsAPI.middleware),
  //    ,       middleware,     .
  //  Redux     : middleware: getDefaultMiddleware => [...getDefaultMiddleware(), RTKQueryApi.middleware]
});

// '  Redux-Persist    Redux :
export const persister = persistStore(store); //      <PersistGate>   index.js
export default store;
