import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react';

// RTK Query       (fetch.js)  Redux (sliceAsyncThunk.js)
// Store  Reducer 
// productsApi = createApi
export const tweetsAPI = createApi({
  reducerPath: 'fetchTweets', //  ,    createSlice  name: 'fetchContacts',
  baseQuery: fetchBaseQuery({
    //fetchBaseQuery -  ,    baseUrl
    baseUrl: 'https://6454a8f5a74f994b33457e38.mockapi.io/', //  fetch.js
  }),

  //   endpoints      :
  tagTypes: ['tweetsSubscribe'],

  //  extraReducers:
  endpoints: builder => ({
    // endpoints -    .  addCase
    // builder.query  get-.    builder.mutation

    getUsers: builder.query({
      query: () => '/users', //     
      //      createAsyncThunk, initialState, sliceAsyncThunk(createSlice), addCase

      providesTags: ['tweetsSubscribe'], //   
      // providesTags: result => {
      //   // console.log('result:', result.length);

      //   return result
      //     ? [
      //         ...result.map(({ id }) => ({ type: 'tweetsSubscribe', id })),

      //         { type: 'tweetsSubscribe', id: 'LIST' },
      //       ]
      //     : [{ type: 'tweetsSubscribe', id: 'LIST' }];
      // },
    }),

    updateTweet: builder.mutation({
      query: data => {
        console.log('data:', data);
        return { url: `/users/${data.id}`, method: 'PUT', body: data };
      },
      invalidatesTags: ['tweetsSubscribe'],
      // invalidatesTags: [{ type: 'tweetsSubscribe', id: 'LIST' }],
    }),

    // deleteTweet: builder.mutation({
    //   query: idTweet => ({ url: `/contacts/${idTweet}`, method: 'DELETE' }),
    //   invalidatesTags: [{ type: 'tweetsSubscribe', id: 'LIST' }], // ,   
    // }),
  }),
});

